import React, { Component } from "react";

import Button from "react-bootstrap/Button";

export class Teste extends React.Component {
  state = {
    nome: "ESTADO INICIAL",
    cor: "cyan"
  };

  tela1 = () => {
    this.setState({
      nome: " Tela 1",
      cor: "green"
    });
  };

  tela2 = () => {
    this.setState({
      nome: " Tela 2",
      cor: "red"
    });
  };

  render() {
    return (
      <div style={{ backgroundColor: this.state.cor }}>
        Meu estado atual: {this.state.nome}
        <br />
        Meu estado de cores atual Ã©: {this.state.cor}
        <div onClick={this.mudar}>MUDEI</div>
        <Button variant="success" onClick={this.tela1}>
          Tela 1
        </Button>{" "}
        <Button variant="danger" onClick={this.tela2}>
          Tela 2
        </Button>{" "}
      </div>
    );
  }
}
