import React, { useEffect } from "react";

const MeuComponente = () => {
	const salvarEstadoNoLocalStorage = (event) => {
		const estadoCheckbox = event.target.checked;
		localStorage.setItem("estadoCheckbox", estadoCheckbox);
	};

	useEffect(() => {
		const estadoCheckbox = localStorage.getItem("estadoCheckbox");
		if (estadoCheckbox === "true") {
			document.getElementById("meu-checkbox").checked = true;
		} else {
			document.getElementById("meu-checkbox").checked = false;
		}
	}, []);

	return (
		<div style={{ backgroundColor: "#993399" }}>
			<h1>Alaaaaaaaaaaaaaaow</h1>
			<input
				type="checkbox"
				id="meu-checkbox"
				onClick={salvarEstadoNoLocalStorage}
			/>
			<label for="meu-checkbox">Meu Checkbox</label>
		</div>
	);
};

export default MeuComponente;
